@import url(https://fonts.googleapis.com/css?family=Lato:100,100i,300,300i,400,400i,700,700i,900,900i);
:root{--belize-hole: #2980b9;--peter-river: #3498db;--nephritis: #27ae60;--emerald: #2ecc71;--green-sea: #16a085;--turquoise: #1abc9c;--pomegranate: #c0392b;--alizarin: #e74c3c;--pumpkin: #d35400;--carrot: #e67e22;--orange: #f39c12;--sunflower: #f1c40f;--midnight-blue: #2c3e50;--wet-asphalt: #34495e;--silver: #bdc3c7;--clouds: #ecf0f1;--asbestos: #7f8c8d;--concrete: #95a5a6;--wisteria: #8e44ad;--amethyst: #9b59b6;--trans-dark-0: rgba(0, 0, 0, 0);--trans-dark-1: rgba(0, 0, 0, 0.1);--trans-dark-2: rgba(0, 0, 0, 0.2);--trans-dark-3: rgba(0, 0, 0, 0.3);--trans-dark-4: rgba(0, 0, 0, 0.4);--trans-dark-5: rgba(0, 0, 0, 0.5);--trans-dark-6: rgba(0, 0, 0, 0.6);--trans-dark-7: rgba(0, 0, 0, 0.7);--trans-dark-8: rgba(0, 0, 0, 0.8);--trans-dark-9: rgba(0, 0, 0, 0.9);--trans-light-0: rgba(255, 255, 255, 0);--trans-light-1: rgba(255, 255, 255, 0.1);--trans-light-2: rgba(255, 255, 255, 0.2);--trans-light-3: rgba(255, 255, 255, 0.3);--trans-light-4: rgba(255, 255, 255, 0.4);--trans-light-5: rgba(255, 255, 255, 0.5);--trans-light-6: rgba(255, 255, 255, 0.6);--trans-light-7: rgba(255, 255, 255, 0.7);--trans-light-8: rgba(255, 255, 255, 0.8);--trans-light-9: rgba(255, 255, 255, 0.9)}body,body *{box-sizing:unset;margin:unset;padding:unset;color:unset;text-decoration:unset}body{display:flex;justify-content:center;align-items:center;margin:2em;height:100vh;box-sizing:border-box;font-family:'Lato', sans-serif;letter-spacing:0.025rem;background:#2980b9;background:var(--belize-hole)}h6{font-size:0.75rem}h5,p,a{font-size:1rem}h4{font-size:1.25rem}h3{font-size:1.5rem}h2{font-size:1.75rem}h1{font-size:2rem}#root{width:100%;height:100%}.flex,.flex-centered,.col-head .col-head-btn{display:flex}.flex-centered{justify-content:center;align-items:center}.text-small{font-size:0.75rem !important}.dark-contrast{background:rgba(0, 0, 0, 0.3);background:var(--trans-dark-3)}.light-contrast{background:rgba(255, 255, 255, 0.3);background:var(--trans-light-3)}.App{display:grid;grid-gap:1.25em;grid-auto-flow:row;height:inherit}.column{border:1px solid rgba(0, 0, 0, 0.1);border:1px solid var(--trans-dark-1);border-radius:6px;-webkit-border-radius:6px;box-shadow:solid 0 1px 2px rgba(0, 0, 0, 0.3);box-shadow:solid 0 1px 2px var(--trans-dark-3);-webkit-box-shadow:solid 0 1px 2px rgba(0, 0, 0, 0.3);-webkit-box-shadow:solid 0 1px 2px var(--trans-dark-3);display:grid;grid-gap:0;grid-template-rows:51px 1fr;opacity:1;-webkit-transition:0.25s linear;transition:0.25s linear}#display-area.min{display:none;-webkit-transition:0.25s linear;transition:0.25s linear}#input-area.min{display:none;-webkit-transition:0.25s linear;transition:0.25s linear}#display-area.max{width:100%;height:100%;grid-column:1 / span 12;grid-row:1 / span 12;-webkit-transition:0.25s linear;transition:0.25s linear}#input-area.max{width:100%;height:100%;grid-column:1 / span 12;grid-row:1 / span 12;-webkit-transition:0.25s linear;transition:0.25s linear}#editor{border:none;width:100%;height:100%;color:rgba(255,255,255,0.75);background:transparent;outline-color:transparent;overflow-y:auto;resize:none}#editor::-webkit-scrollbar{height:6px;width:6px}#editor::-webkit-scrollbar-track{border-radius:10px;-webkit-border-radius:10px;background:rgba(0, 0, 0, 0.2);background:var(--trans-dark-2)}#editor::-webkit-scrollbar-track-piece{background:transparent}#editor::-webkit-scrollbar-thumb{border-radius:10px;-webkit-border-radius:10px;background:rgba(255, 255, 255, 0.2);background:var(--trans-light-2);border:1px solid rgba(0, 0, 0, 0.1);border:1px solid var(--trans-dark-1)}#editor::-webkit-scrollbar-thumb:hover{box-shadow:inset 0 0 6px rgba(0, 0, 0, 0.1);box-shadow:inset 0 0 6px var(--trans-dark-1);-webkit-box-shadow:inset 0 0 6px rgba(0, 0, 0, 0.1);-webkit-box-shadow:inset 0 0 6px var(--trans-dark-1)}#editor::-webkit-scrollbar-corner{background:transparent}#preview::-webkit-scrollbar{height:6px;width:6px}#preview::-webkit-scrollbar-track{border-radius:10px;-webkit-border-radius:10px;background:rgba(0, 0, 0, 0.3);background:var(--trans-dark-3)}#preview::-webkit-scrollbar-track-piece{background:transparent}#preview::-webkit-scrollbar-thumb{border-radius:10px;-webkit-border-radius:10px;background:rgba(255,255,255,0.5);border:1px solid rgba(0, 0, 0, 0.1);border:1px solid var(--trans-dark-1)}#preview::-webkit-scrollbar-thumb:hover{box-shadow:inset 0 0 6px rgba(0, 0, 0, 0.1);box-shadow:inset 0 0 6px var(--trans-dark-1);-webkit-box-shadow:inset 0 0 6px rgba(0, 0, 0, 0.1);-webkit-box-shadow:inset 0 0 6px var(--trans-dark-1)}#preview::-webkit-scrollbar-corner{background:transparent}.col-body{grid-row:2/-1;padding:1.25em;border-bottom-left-radius:inherit;-webkit-border-bottom-left-radius:inherit;-moz-border-radius-bottomleft:inherit;border-bottom-right-radius:inherit;-webkit-border-bottom-right-radius:inherit;-moz-border-radius-bottomright:inherit;position:relative}.col-head{padding:1em 1.25em;border-top-left-radius:inherit;-webkit-border-top-left-radius:inherit;-moz-border-radius-topleft:inherit;border-top-right-radius:inherit;-webkit-border-top-right-radius:inherit;-moz-border-radius-topright:inherit;display:grid;grid-gap:0;align-self:start;grid-template-columns:repeat(2, 1fr);align-items:center}.col-head h1{display:grid;grid-gap:.75em;grid-template-columns:auto 1fr;align-items:center;font-size:1rem;font-weight:400}.col-head .col-head-btn{border:none;justify-content:flex-end;align-items:center;width:auto;height:auto;background:transparent;cursor:pointer;outline:none}.col-head .col-head-btn svg{width:1.5rem;height:1.5rem}#input-header{background-color:#2c3e50;background-color:var(--midnight-blue);color:#fff}#input-header #input-btn{color:rgba(255, 255, 255, 0.6);color:var(--trans-light-6)}#input-header #input-btn:hover{color:rgba(255, 255, 255, 0.2);color:var(--trans-light-2)}#input-body{background:#34495e;background:var(--wet-asphalt)}#display-header{background:#f1c40f;background:var(--sunflower);color:rgba(0, 0, 0, 0.6);color:var(--trans-dark-6)}#display-header #display-btn{color:rgba(0, 0, 0, 0.6);color:var(--trans-dark-6)}#display-header #display-btn:hover{color:rgba(0, 0, 0, 0.2);color:var(--trans-dark-2)}#display-body{background:#fff;align-items:center;justify-content:center}#preview{position:absolute;left:1.25em;right:1.25em;top:1.25em;bottom:1.25em;width:inherit;height:auto;overflow-y:auto !important}@media screen and (min-width: 0px){body{padding:2rem}.App{grid-template-columns:repeat(12, 1fr);grid-template-rows:auto}#input-area{grid-column:span 12;grid-row:span 1}#display-area{grid-column:span 12;grid-row:span 4}}@media screen and (min-width: 800px){body{padding:4rem}#input-area{grid-column:1 / span 4;grid-row:1 / -1}#display-area{grid-column:5 / -1;grid-row:1 / -1}}

